.transactionHistory {
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;

  background-color: var(--color-white);
  border: 1px solid;
  border-radius: 5px;
  outline: none;
}

.thead {
  background-color: var(--color-blue);
  text-transform: uppercase;
}

.th {
  padding: 20px;
}

.td {
  padding: 10px;
  text-align: center;
  text-transform: capitalize;
}

